ul {
  list-style: none;
}

.gallery {
  max-width: calc(100% - 64px);
  grid-gap: 24px;
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  justify-content: center;
  margin: 0 auto;
  padding: 0;
  list-style: none;
  display: grid;
}

.hidden {
  opacity: 0;
  pointer-events: none;
  position: absolute;
}

.gallery__item {
  padding: 30px;
}

.gallery__item p {
  margin-top: 0;
  margin-bottom: 8px;
}

.gallery__thumb {
  height: 200px;
  margin-bottom: 16px;
}

.gallery__item img {
  object-fit: cover;
  max-width: 100%;
  height: 100%;
}

#header {
  display: flex;
}

#header li {
  margin-left: 20px;
}

#header li button {
  min-width: 100px;
  background: none;
  border: 3px solid #000;
  border-radius: 5px;
  padding: 10px;
  font-size: medium;
  font-weight: 600;
  transition: all .15s ease-in-out;
}

#header li button:hover {
  color: #fff;
  background-color: #000;
}

